import React from 'react';
import {connect} from 'react-redux';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';

import {Div, Panel, Alert, Group, Button, PanelHeader, Header, Card, CardGrid} from "@vkontakte/vkui"

class HomePanelBase extends React.Component {

    state = {
        showImg: false
    };

    showImg = () => {
        this.setState({showImg: true});
    };

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Нет',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: 'Да',
                    autoclose: true,
                    action: {
                        href: 'https://vk.com/cm_daddy'
                    }
                }]}
                onClose={() => this.props.closePopout()}
            >
                <p>Вы действительно хотите перейти по ссылке?</p>
            </Alert>
        );
    }

    render() {
        const {id, setPage, withoutEpic} = this.props;

        return (
            <Panel id={id}>
                <PanelHeader>
                    Главная
                </PanelHeader>

                <br />
                <Group header={<Header mode="secondary">Баланс</Header>}>
                    
                    {/* 

                    <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => this.openPopout()}>Открыть алерт</Button>
                    </Div>

                    <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => this.props.openModal("MODAL_PAGE_BOTS_LIST")}>Открыть
                            модальную страницу</Button>
                    </Div>

                    {withoutEpic && <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => setPage('modal', 'filters')}>Открыть модальное окно</Button>
                    </Div>}

                    {this.state.showImg && <Div className="div-center">
                        
                    </Div>} */}
                    
                </Group>
                    <div className="money">0
                        <a className="ruble">₽</a>
                    </div>

                    <br />

                    {/* <CardGrid>
                        <Card size="l">
                            <div style={{ height: 96, "background-color": 'rgb(244, 95, 95)', "border-radius": '13px', "color": 'white', "text-indent": '6px' }}>
                                Тут что-то важное...
                            </div>
                        </Card>
                    </CardGrid> */}


                    {/* <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => setPage('home', 'groups')}>Список моих групп</Button>
                    </Div> */}
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
