import React from 'react';
import {connect} from 'react-redux';

import {setPage, setStory} from "../../store/router/actions";
import {setActiveTab, setScrollPositionByID} from "../../store/vk/actions";
import {restoreScrollPosition} from "../../services/_functions";

import {
    Div,
    Panel,
    Group,
    CellButton,
    PanelHeader,
    FixedLayout,
    HorizontalScroll,
    TabsItem,
    Alert,
    Tabs,
    Button,
    Snackbar,
    Avatar
} from "@vkontakte/vkui";

import {
    closePopout, 
    openModal, 
    openPopout
} from '../../store/router/actions';

import { 
    Icon28LogoVkOutline, 
    Icon28LightbulbStarOutline, 
    Icon28HelpOutline, 
    Icon28SnowflakeOutline, 
    Icon28SettingsOutline,
    Icon16ErrorCircle,
    Icon56GhostOutline
} from '@vkontakte/icons';

class HomePanelProfile extends React.Component {
    constructor(props) {
        super(props);
    
        this.state = {
            text: '',
            snackbar: null
        };
    
        this.openBaseWithAction = this.openBaseWithAction.bind(this);
      }

    openBaseWithAction () {
        if (this.state.snackbar) return;
        this.setState({ snackbar:
            <Snackbar
                onClose={() => this.setState({ snackbar: null })}
                before={<Avatar size={22} style={{ background: 'white' }}><Icon16ErrorCircle fill="#FF0000" width={24} height={24} /></Avatar>}
            >
            Данная функция отключена
            </Snackbar>
        });
    }

    componentDidMount() {
        restoreScrollPosition();
    }

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Понятно',
                    autoclose: true,
                    style: 'cancel',
                }
            ]}
                onClose={() => this.props.closePopout()}
            >   
                <div style={{ 
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
            justifyContent: 'center',
            textAlign: 'center',
            padding: 32, }}>
                <Icon56GhostOutline fill="#20E5F3" width={96} height={96} />
                <h1>Тех. Работы</h1>
                <p>Введутся Тех. работы..<br/>Как всё будет готово, мы Вас оповестим в нашей группе.</p>
                </div>
                
            </Alert>
        );
    }

    render() {
        const {id, setPage} = this.props;
        const boxStyle = {marginTop: 56};

        return (
            <Panel id={id}>
                <PanelHeader>
                    Ещё
                    </PanelHeader>
                
                {/* <FixedLayout vertical="top">
                    <Tabs theme="header" mode="default">
                        <HorizontalScroll id="EXAMPLE_TABS_LIST">
                            <TabsItem
                                onClick={() => this.setTab('modal')}
                                selected={this.state.activeTab === 'modal'}
                            >
                                Модальное окно
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test')}
                                selected={this.state.activeTab === 'test'}
                            >
                                Для теста
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test2')}
                                selected={this.state.activeTab === 'test2'}
                            >
                                Для теста 2
                            </TabsItem>
                            <TabsItem
                                onClick={() => this.setTab('test3')}
                                selected={this.state.activeTab === 'test3'}
                            >
                                Для теста 3
                            </TabsItem>
                        </HorizontalScroll>
                    </Tabs>
                </FixedLayout> */}

                {/* <Group style={boxStyle}>
                    {this.state.activeTab === 'modal' && <CellButton onClick={() => setPage('modal', 'filters')}>
                        Открыть модальное окно
                    </CellButton>}

                    {this.state.activeTab !== 'modal' && <Div>{this.state.activeTab}</Div>}
                </Group> */}

                    {this.state.text && <Group><Div>{this.state.text}</Div></Group>}

                    {this.state.snackbar}

                <Group>

                    <div>
                        <Button href="https://vk.com/gamecklicker" target="_blank" style={{"border-radius": '3px', "line-height": '50px', "margin": '30px auto', "padding": '0 20px', "background": 'blue', "color": 'white', "text-align": 'center', "border": '1px solid blue', "-moz-box-shadow": '0 0 20px #555', "-webkit-box-shadow": '0 0 20px #555', "box-shadow": '0 0 20px blue', "margin-bottom": '5px', "width": '96%'}} stretched className="button-group" before={<Icon28LogoVkOutline />}>Группа ВКонтакте</Button>
                    </div>
                    
                    <div>
                        <Button href="https://vk.com/topic-203633937_47497056" target="_blank" style={{"border-radius": '3px', "line-height": '50px', "margin": '30px auto', "padding": '0 20px', "background": '#F2C40C', "color": 'white', "text-align": 'center', "border": '1px solid #F2C40C', "-moz-box-shadow": '0 0 20px #555', "-webkit-box-shadow": '0 0 20px #555', "box-shadow": '0 0 20px #F2C40C', "margin-bottom": '5px', "width": '96%'}} stretched className="button-group" before={<Icon28LightbulbStarOutline />}>У меня есть идея!</Button>
                    </div>

                    <div>
                        <Button href="https://vk.com/im?sel=-203633937" target="_blank" style={{"border-radius": '3px', "line-height": '50px', "margin": '30px auto', "padding": '0 20px', "background": '#DF3FF0', "color": 'white', "text-align": 'center', "border": '1px solid #DF3FF0', "-moz-box-shadow": '0 0 20px #555', "-webkit-box-shadow": '0 0 20px #555', "box-shadow": '0 0 20px #DF3FF0', "margin-bottom": '5px', "width": '96%'}} stretched className="button-group" before={<Icon28HelpOutline />}>Остались вопросы?</Button>
                    </div>

                    <div>
                        <Button disabled="true" onClick={this.openBaseWithAction} style={{"border-radius": '3px', "line-height": '50px', "margin": '30px auto', "padding": '0 20px', "background": '#54D8CF', "color": 'white', "text-align": 'center', "border": '1px solid #54D8CF', "-moz-box-shadow": '0 0 20px #555', "-webkit-box-shadow": '0 0 20px #555', "box-shadow": '0 0 20px #54D8CF', "margin-bottom": '5px', "width": '96%'}} stretched className="button-group" before={<Icon28SnowflakeOutline />}>Включить снег</Button>
                    </div>

                    <div>
                        <Button onClick={() => this.openPopout()} style={{"border-radius": '3px', "line-height": '50px', "margin": '30px auto', "padding": '0 20px', "background": '#E43D3D', "color": 'white', "text-align": 'center', "border": '1px solid #E43D3D', "-moz-box-shadow": '0 0 20px #555', "-webkit-box-shadow": '0 0 20px #555', "box-shadow": '0 0 20px #E43D3D', "margin-bottom": '5px', "width": '96%'}} stretched className="button-group" before={<Icon28HelpOutline />}>Почему не работает страница "Лента"?</Button>
                    </div>

                </Group>
            </Panel>
        );
    }

}

const mapStateToProps = (state) => {
    return {
        activeTab: state.vkui.activeTab,
    };
};

const mapDispatchToProps = {
    setPage,
    setActiveTab,
    setScrollPositionByID,
    openPopout,
    closePopout,
    openModal
};

export default connect(mapStateToProps, mapDispatchToProps)(HomePanelProfile);
